@font-face {
       
 font-family: 'Amazon Ember';
       
 src: url(../assets/fonts/Amazon-Ember-Medium.ttf) format('truetype');
       

   }
   


// USAGE
body {
       
 font-family: 'Amazon Ember', sans-serif;
       

   }
   
.wrapper {
       
 width: 100%;
       
 height: 100vh;
       
 display: flex;
       
 overflow: hidden;
       
 align-items: center;
       
 user-select: none;
       

   }
   

.wrapper::before {
       
 content: '';
       
 position: absolute;
       
 top: 0;
       
 left: 0;
       
 width: 100%;
       
 z-index: -1;
       
 height: 100%;
       
 background: linear-gradient(92.37deg, #2F3133 1.99%, rgba(0, 0, 0, 0) 57.25%);
       

   }
   

aside {
       
 height: 34vw;
       
 width: 20vw;
       
 display: flex;
       
 flex-direction: column;
       
 gap: 2vw;
       
 margin-top: -9vw;
       
 margin-left: 2%;
       

   }
   

aside img {
       
 max-width: 15vw;
       
 margin: 0 auto 1vw;
       

   }
   

aside .item {
       
 position: relative;
       
 width: 100%;
       
 height: 5vw;
       
 display: flex;
       
 padding-left: 25px;
       
 align-items: center;
       
 margin-bottom: 1vw;
       
 border-radius: 20px 0 0 0;
       
 border: 1px solid #12AC5A1818;
       
 background: linear-gradient(270deg, rgba(17, 17, 17, 0) 0%, rgba(17, 17, 17, 0.787879) 40.62%, #2B2B2B 60.94%, rgba(17, 17, 17, 0.82) 100%);
       

   }
   

aside .item:hover {
       
 transform: scale(1.02);
       
 transition-duration: 200ms;
       

   }
   

aside .item:hover::before {
       
 opacity: 1;
       

   }
   

aside .item .info {
       
 display: flex;
       
 color: #fff;
       
 flex-direction: column;
       

   }
   

aside .item .info span {
       
 margin: 0;
       
 font-size: 10px;
       

   }
   

aside .item .info p {
       
 margin: 0;
       
 font-size: .85vw;
       
 font-weight: 600;
       
 letter-spacing: 0.5px;
       
 text-transform: uppercase;
       

   }
   

aside .time-row {
       
 width: 100%;
       
 display: flex;
       
 margin-top: 1vw;
       
 align-items: center;
       
 justify-content: center;
       

   }
   

aside .time-column {
       
 width: 50%;
       
 display: flex;
       
 padding: 10px;
       
 font-weight: 600;
       
 border-radius: 5px;
       
 align-items: center;
       
 justify-content: center;
       
 color: rgba(204, 204, 204, 0.336);
       
 border: 2px solid rgba(204, 204, 204, 0.336);
       

   }
   

aside .time-row span {
       
 margin: 0 6px;
       

   }
   

aside footer {
       
 position: relative;
       
 display: flex;
       
 margin-left: 15px;
       
 flex-direction: column;
       

   }
   

aside footer .title {
       
 display: flex;
       
 align-items: center;
       

   }
   

aside footer .title i {
       
 font-size: 25px;
       
 color: #fff;
       
 margin-right: 10px;
       

   }
   

aside footer::before {
       
 content: '';
       
 position: absolute;
       
 top: 5px;
       
 left: -30px;
       
 width: 10px;
       
 height: 100%;
       
 border-radius: 2px;
       
 background: #1d1d1d;
       

   }
   

