   }
   

aside footer p {
       
 color: #fff;
       
 display: flex;
       
 letter-spacing: .5px;
       
 flex-direction: column;
       
 text-transform: uppercase;
       

   }
   

aside footer p small {
       
 opacity: .7;
       

   }
   

aside footer .spawns {
       
 display: flex;
       
 margin-top: 7px;
       
 align-items: center;
       

   }
   

.flex {
       
 display: flex;
       
 align-items: center;
       
 gap: 1vw;
       
 svg {
           
 color: #6a6767;
           
 width: 1.7vw;
           
 height: 1.7vw;
           
 
       }
       

   }
   
